<template>
  <b-row class="mb-1">
    <b-col style="text-align: left">
      <b-form @submit="onSubmit" @reset="onReset">
        <b-form-group
          id="userid-group"
          label="작성자:"
          label-for="userid"
          description="작성자를 입력하세요."
        >
          <b-form-input
            id="userid"
            :disabled="isUserid"
            v-model="article.userid"
            type="text"
            required
            placeholder="작성자 입력..."
          ></b-form-input>
        </b-form-group>

        <b-form-group id="content-group" label="내용:" label-for="content">
          <b-form-textarea
            id="content"
            v-model="article.content"
            placeholder="내용 입력..."
            rows="10"
            max-rows="15"
          ></b-form-textarea>
        </b-form-group>

        <b-button
          type="submit"
          variant="primary"
          class="m-1"
          v-if="this.type === 'register'"
          >글작성</b-button
        >
        <b-button type="submit" variant="primary" class="m-1" v-else
          >글수정</b-button
        >
        <b-button type="reset" variant="danger" class="m-1">초기화</b-button>
      </b-form>
    </b-col>
  </b-row>
</template>

<script>
import http from "@/api/http";

export default {
  name: "AnswerInputItem",
  data() {
    return {
      article: {
        answerno: 0,
        userid: "",
        content: "",
      },
      isUserid: false,
    };
  },
  props: {
    type: { type: String },
    questionno: Number,
  },
  created() {
    //alert(this.$route.params.questionno);
    if (this.type === "modify") {
      //modify 일단 보류
      http
        .get(`/aboard/answer/${this.$route.params.answerno}`)
        .then(({ data }) => {
          this.article = data;
        });
      this.isUserid = true;
    }
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();

      let err = true;
      let msg = "";
      !this.article.userid &&
        ((msg = "작성자 입력해주세요"),
        (err = false),
        this.$refs.userid.focus());
      err &&
        !this.article.content &&
        ((msg = "내용 입력해주세요"),
        (err = false),
        this.$refs.content.focus());

      if (!err) alert(msg);
      else
        this.type === "register" ? this.registArticle() : this.modifyArticle();
    },
    onReset(event) {
      event.preventDefault();
      this.article.answerno = 0;
      this.article.content = "";
      this.$router.push({ name: "answerList" });
    },
    registArticle() {
      //alert(this.$route.params.questionno);
      http
        .post(`/aboard`, {
          questionno: this.$route.params.questionno,
          userid: this.article.userid,
          content: this.article.content,
        })
        .then(({ data }) => {
          let msg = "등록 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "등록이 완료되었습니다.";
          }
          alert(msg);
          this.moveList();
        });
    },
    modifyArticle() {
      http
        .put(`/aboard/${this.article.answerno}`, {
          answerno: this.article.answerno,
          questionno: this.$route.params.questionno,
          userid: this.article.userid,
          content: this.article.content,
        })
        .then(({ data }) => {
          let msg = "수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "수정이 완료되었습니다.";
          }
          alert(msg);
          // 현재 route를 /list로 변경.
          this.$router.push({
            name: "questionDetail",
            params: { articleno: this.$route.params.questionno },
          });
        });
    },
    moveList() {
      this.$router.push({
        name: "questionDetail",
        params: { articleno: this.$route.params.questionno },
      });
    },
  },
};
</script>

